footer, .content {
  justify-content: center;
  align-items: center;
  display: flex;
}

html, body, p, ol, ul, li, dl, dt, dd, blockquote, figure, fieldset, legend, textarea, pre, iframe, hr, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button, input, select {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

img, video {
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td, th {
  padding: 0;
}

*, :after, :before {
  box-sizing: border-box;
}

html {
  color: #272727;
  background: #fff;
  font-family: Roboto, sans-serif;
  font-size: .520833vw;
  line-height: 1;
}

a {
  color: inherit;
  outline: none;
  text-decoration: none;
}

button {
  color: inherit;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
}

h1, h2, h3 {
  font-weight: bold;
}

img {
  pointer-events: none;
}

h1 {
  font-size: 4rem;
}

p {
  font-size: 1.2rem;
}

main {
  min-height: 96vh;
}

.nav {
  width: 100%;
  padding: 2rem 5%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.nav__left {
  flex-basis: 40%;
}

.nav__right {
  width: 100%;
  justify-content: flex-end;
  align-items: center;
  gap: 7.5rem;
  font-size: 1.25rem;
  display: flex;
}

.nav__logo {
  height: auto;
  width: max-content;
  display: block;
}

.nav__logo img {
  max-width: 64px;
  height: auto;
}

.nav__menu__link {
  color: #4d4d4d;
}

.nav__menu__link:hover {
  color: #000;
}

footer {
  height: 4vh;
}

